.barra{
    display: grid;
    grid-template-rows: 1fr, 1fr, 1fr;
    row-gap: 3rem;
    align-items: center;
    margin-top: 2rem;

    
    @include tablet{
        grid-template-columns: 1fr  6fr 1fr;
        column-gap: 2rem;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 2rem;
    }
}

.izquierda{
    img {
        width: 50%;
        margin: auto;
    }
    grid-row: 2/3;
    margin: 0;
    margin: 0;

    @include tablet{
        align-items: center;
        grid-column: 1/2;
        grid-row: 1/2;

        img{ 
            width: 15rem;
        }

    }
}
.centro {
    grid-row: 3/4;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    margin-bottom: 1rem;
    
    @include tablet{
        align-items: center;
        grid-column: 2/3;
        grid-row: 1/2;

    }
}

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: 1/2;
    order: 0;
    @include tablet{
        flex-direction: row;
        align-items: flex-end;
        grid-column: 3/4;
        padding-right: 1.5rem;

    }

    .dark-mode-boton {
        width: 5rem;
        margin-bottom: 2rem;
        filter: invert(100%);
    
        &:hover {
            cursor: pointer;
        }
    
        @include tablet {
            margin-bottom: 0;
            margin-left: 2rem;
            width: 3rem;
            order: 2;
        }
    }

    .sesionBtn {
        width: 5rem;
        margin-top: 1rem;
        margin-bottom: 3rem;

        &:hover {
            cursor: pointer;
        }
        @include tablet {
            margin-bottom: 0;
            margin-top: 0;
            width: 3rem;
            order: 1;
        }
    }
}  

.navegacion {
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear;

   @include tablet {
       display: flex;
       align-items: flex-start;
       opacity: 1;
       visibility: visible;
       margin-bottom: 0;
}
    a{

        color: $blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        position: relative;

        @include tablet {
            margin-right: 2rem;
            font-size: 1.8rem;

            &:last-of-type{
                margin-right: 0;
            }
        }

    }
}
//Menu de hamburguesa

.mobile-menu {
    position: relative;
    @include tablet {
        display: none;
    }
    img {
        width: 5rem;
    }
}

.mostrar {
	visibility: visible;
	opacity: 1;
}