.blog {
    margin: 1rem 0;
    padding: 0 5rem 0 0;
}

.entrada-blog {
    margin-bottom: 1rem;
  
    @include tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    picture {
        height: 25rem;
        
          img{
              height: 100%;
              object-fit: contain; //Para evitar que la imagen se distorsione
          }
      }

.informacion-meta {
    span{
        color: $amarillo;
    }
}
.texto-entrada {
    height: 25rem;
    margin-top: 2rem;
    overflow: hidden;


    a{
        color: $negro;
        overflow: clip;
        white-space: wrap;

        h3 {
            text-align: start;
            margin: 0;
            line-height: 1.4;
    
            
    
            &::after{
                content: "";
                display: block;
                width: 30rem;
                height: .4rem;
                background-color: $verde;
                margin-top: .5rem;
            }
        }
    }

    @include tablet {
        margin-top: 0;
    }


}
}
