@include tablet {
    .contenedor-demos{
        @include grid(3, 2rem);
    }
}

.demo {
    background-color: lighten($gris, 5);
    border: 1px solid darken($gris, 5);
    margin-bottom: 2rem;
    overflow: hidden;

    picture {
        height: 30rem;
        
          img{
              height: 100%;
              object-fit: contain; //Para evitar que la imagen se distorsione
          }
      }
    .contenido-demo{
        padding: 2rem;
    }
    .contenido-demo > p{
        height: 10rem;
        overflow: hidden;
        white-space: wrap;
      

    }

    h3, p {
        margin: 0;
    }

    h3{
        font-weight: $bold;
        overflow: hidden;
        white-space: wrap;
        text-overflow: ellipsis;

    }
}

p.precio{
    text-align: center;
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
    padding-top: 2rem;
}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;

    li {
        flex: 1;
        display: flex;
        align-items:  center;
        justify-content: space-evenly;

        div {
            display: flex;
            align-items: center;
        
            img {
                flex: 0 0 2rem;
                margin-right: 0.3rem;
            }
            p {
                font-weight: $black;
            }
        }
        
    }
}
    .nombresIconos-caracteristicas {
        display: flex;
        list-style: none;
        padding: 0;
        text-align: center;

    
        li {
            flex: 1;
        }
    }

    .demos{
        p{
            text-align: center;
        }
    }
