.botones {
    display: flex;
    justify-content: space-around;
}


.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $regular;
    text-transform: uppercase;
    margin: 1rem 0;
    font-size: 1.2rem;

    &.error{
        background-color: $rojo;
    }

    &.exito {
        background-color: $verde;
    }
}   
.tDemos, .tBlogs, .tColabs {

    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
    background-color: $grisOscuro;
    padding-bottom: 20rem;
    

    th {
        color: $blanco;
        padding: .5rem;
        text-align: center;
        }

    }

    tbody {
        td {
            max-width: 15rem;
            padding: 1rem 1rem 0;
            text-align: center;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;

            &:last-of-type {
                width: 20rem;
            }

            .boton-amarillo-block,
            .boton-rojo-block {
                margin: 0.5rem 0;
                width: 100%;
            }
        }

        td>ul{
            list-style-type: none;
            text-transform: capitalize;
            text-align: start;
            padding-left: 0;
        }
    }
}
.tDemos {    
    thead {    
        th {
            &:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6), &:nth-child(7), &:nth-child(8), &:nth-child(9), &:nth-child(10){
                display: table-column;
            }
            @include desktop{  
                &:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6), &:nth-child(7), &:nth-child(8), &:nth-child(9), &:nth-child(10){
                    display: table-cell;
                }
            }
        }

    }
    tbody {
        td {
            &:nth-child(2){
                text-align: start;
            }
            &:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6), &:nth-child(7), &:nth-child(8), &:nth-child(9), &:nth-child(10){
                display: table-column;
            }
            @include desktop{  
                &:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6), &:nth-child(7), &:nth-child(8), &:nth-child(9), &:nth-child(10){
                    display: table-cell;
                }
            }

        .imagen-tabla{
            width: 15rem;
            max-height: 15rem;
            margin: 0 auto;
            }
        }
    }
}


.tBlogs {

    
    thead {
        th {
            &:nth-child(3), &:nth-child(4){
                display: table-column;
            }
            @include desktop{  
                &:nth-child(3), &:nth-child(4){
                    display: table-cell;
                }
            }
        }

    }
    tbody {
        td {
            &:nth-child(2){
                text-align: start;
            }
            &:nth-child(3), &:nth-child(4){
                display: table-column;
            }
            @include desktop{  
                &:nth-child(3), &:nth-child(4){
                    display: table-cell;
                }
            }

        .imagen-tabla{
            width: 15rem;
            max-height: 15rem;
            margin: 0 auto;

   
            }
        }
    }
}


.tColabs {    
    thead {
        th {
            &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(5){
                display: table-column;
            }
            @include desktop{  
                &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(5){
                    display: table-cell;
                }
            }
        }

    }
    tbody {
        td {
            &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(5){
                display: table-column;
            }
            @include desktop{  
                &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(5){
                    display: table-cell;
                }
            }
        }
    }
}


.imagen-pequeña{
    width: 20rem;
}

hr {
    margin: 10rem 0;
}