.demoPage {    
    .centrarDP{
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;

        .precio{
            text-align: center;
            font-size: 6rem;
            padding-top: 0;
        }


        .nombresIconos {
            display: flex;
            list-style: none;
            padding: 0;
            text-align: center;
    
        
            li {
                flex: 1;
            }
        }
    .iconos {
        display: flex;
        list-style: none;
        padding: 0;
        justify-content: space-around;

        div {
            display: flex;

            img {
                width: 10rem;
            }

            p{
                font-size: 2.8rem;
            }

        }

        }

        p {
            text-align: center;
            font-size: 2.2rem;
        }
}
}