.formulario {
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0 ;
    }
    legend {
        font-size: 2rem;
        color: $grisOscuro;
    }
    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]),
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid  $gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    //remover los botones de los input number
    input[type=number]::-webkit-inner-spin-button,
    input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
    }

    textarea{
        height: 20rem;
    }

    .forma-contacto {
        max-width: 50rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    input[type="radio"] {
        margin: 0 auto;
        width: auto;
    }

    .checkbox-idiomas {
        max-width: 40rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .checkbox-canales {
        max-width: 80rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    input[type="checkbox"] {
        margin: 0 4rem 0 0;
        width: auto;
        appearance: none;
        background-color: lighten($gris, 5);
        border: 0.1rem solid $gris;
        border-radius: 0.4rem;
        outline: max(0.2rem, 0.05rem) solid lighten($grisOscuro, 9);
        outline-offset: max(0.2rem);
    }

    input[type="checkbox"]:checked {
        background-color: lighten($verde, 9);
    }

    .custom-file-input::-webkit-file-upload-button {
        visibility: hidden;
      }
      .custom-file-input::before {
        content: 'Elige un archivo';
        display: inline-block;
        background-color: $gris;
        border: 0.3rem solid lighten($gris, 7);
        border-radius: .7rem;
        padding: 1rem;
        outline: none;
        white-space: nowrap;
        cursor: pointer;
        font-weight: $bold;
        font-size: 1.6rem;
        color: $grisOscuro;
      }

      .custom-file-input::after {
        display: none;
      }
      .custom-file-input:hover::before {
        background-color: $grisOscuro;
        border: 0.3rem solid lighten($grisOscuro, 7);
        color: $blanco;
      }

}

.reducir {
    width: 70%;
    margin: 0 auto;
    @include tablet {
        width: 30%;
    }
  
    input[type="password"], input[type="text"] {
        margin-bottom: 0;
        max-width: 100%;
    }
    .forma-contacto {
        align-items: center;
        position: relative;
        width: 100%;
        margin: 0;
    }
}
.imgPass {
    width: 2.5rem;
    height: 2rem;
    position: absolute;
    right: 1rem;

    &:hover{
        cursor: pointer;
    }
}