.tarifas{
    display: flex;
    flex-direction: column;
    align-items: center;


    @include desktop {
        flex-direction: row;
        justify-content: center;
        align-items: start;
    }
    .card {

        border-radius: 5rem;
        max-width: 40rem;
        text-align: center;

        @include desktop {
            min-width: 30rem;
            margin: 0 0.2rem;
        }

        &:last-of-type{
            .titulo{
                background: linear-gradient(115deg, darken($amarillo, 4) 43%, lighten($amarillo, 6) 57%);
            }
        }

        .titulo {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 10rem;
            border-radius: 5rem 5rem 0 0;
            background: linear-gradient(115deg, darken($turquesa, 16) 43%, darken($turquesa, 6) 57%);
            margin: 2rem auto 0;

        }

        .centro{
            min-height: 100%;
            margin: 0;
            background: linear-gradient(115deg, lighten($turquesa, 11) 43%, lighten($turquesa, 20) 57%);
            padding: 2rem 0.5rem;
        }

        .costos {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 20rem;
            border-radius: 0 0 5rem 5rem;
            background: linear-gradient(115deg, darken($turquesa, 16) 43%, darken($turquesa, 6) 57%);
            margin: 0 auto;

        }

        hr{
            margin: 1rem;
            width: 15rem;
            border-color: $negro;
        }
        h4{

            margin: 0;
            padding: 1rem;
        }

        p{
            padding: 0.2rem 1rem;
            font-size: 1.6rem;
            margin: 0;
        }

        ul{
            margin: 0.5rem 0 2rem;
        }

        .pFontPeq{
            font-size: 1.3rem;
            margin: 0;
        }

        .margTopBotm{
            margin: 0;
        }
        .bold{
            font-weight: $black;
        }
        ul{
            
            text-align: start;
            padding: 0 4rem;

            li{
                font-size: 1.3rem;
            }
        }
        .precio{
            font-size: 2.4rem;
            font-weight: $bold;
            margin: 0 0 0 0.5rem;
        }
        
    }
}
.letraChica {
    font-size: 1rem;
    text-align: center;
}