.blogPage {    
    .centrarDP{
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;


        p {
            text-align: start;
            font-size: 2rem;
            margin: 0;
            font-weight: 300;

            &:first-of-type{
                margin: 2rem 0;
            }
        }

        h3, h4{
            text-align: start;
        }

        span {
            font-weight: 500;
        }

        .botonesBlog{
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include tablet {
                flex-direction: row;
                gap: 2rem;
            }
        }

        .boton-verde-block, .boton-amarillo-block {
            min-width: 20rem;
        }
}
}