    .no-webp .imagen-contacto{
        background-image: url('../img/rocketL2.png');
    }
    
    .webp .imagen-contacto{
        background-image: url('../img/rocketL2.webp');
    }
    .imagen-contacto {
        background-position: center center;
        background-size: cover;
        padding: $separacion * 3 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 10rem;
    
        h2{
            font-size: 5rem;
            font-weight: $black;
            color: $blanco;
            margin-left: 1rem;
            margin-right: 1rem;
            line-height: 1.3;
            margin-bottom: 0.5rem;
        }
        p{
            font-size: 2.4rem;
            color: $blanco;
            margin-left: 1rem;
            margin-right: 1rem;
            margin-top: 0.5rem;
            text-align: center;
        }
    }

    .noMTop {
        margin-top: 0;
        font-size: 1.6rem;
        text-align: center;
    }

    input#telefono{
        margin-bottom: 0;
    }

    .fotobg{
        background-image: url('../img/contactUs.webp');
        background-position: center center;
        background-size: cover;
        padding: $separacion * 3 0;
        min-width: 100%;
        min-height: 100%;


        @include tablet {
            height: 65rem;
        }

        @include desktop {
            height: 80rem;
        }

}


