.footer {
    padding: calc($separacion / 2) 0 0.3rem 0;
    background-color: $grisOscuro;
    margin-bottom: 0;
    margin-top: 6rem;
    padding: 3rem 0;

    .contenido-footer {
        padding-top: calc($separacion / 2) 0 0 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .navegacion {
        visibility: visible;
        opacity: 1;
    }
}

.loginFormat{
    margin: 17rem auto;
    }

    .finalFooter{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 3rem;

        @include tablet{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin: 0  2rem;
        }

        .copyright {
            color: $blanco;
            text-align: center;
            font-size: 1.2rem;
        }

        .politicas{
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            text-align: center;
            align-items: center;

            a{
                color: $blanco;
                font-size: 1.2rem;
            }
            span{
                margin: 0 0.2rem;
                color: $blanco;
                font-size: $bold;
            }

        }
    }
