.home {
        background-image: url('../img/header.jpg');
        background-image: url('../img/header.webp');

        background-position: center center;
        background-size: cover;
}
.contenido-home {

    position: relative;
    background: linear-gradient(270deg, rgba(255, 166, 0, 0.47) 40%, rgba(0, 0, 0, 0.648) 60%);
    height: 100rem;
    display: flex;
    flex-direction: column;


    h1 {
        text-align: center;
        color: $blanco;
        font-weight: $bold;
        line-height: 1.3;
        max-width: 80rem;
        margin: 5rem 2rem;
    }

    @include tablet {
        min-height: 80rem;
        height: 90%;
        width: 100%;


        h1{        
            text-align: left;
            margin: 5rem 3rem;
            font-size: 4.4rem;
            text-transform: capitalize;
            max-width: 65rem;
        }
    }
}

.homeTitles {
    h2 {
        margin: 12rem 0 6rem;

        text-transform: uppercase;
    }
}