.dark-Mode {
    background-color: darken($grisOscuro, 10);

    p, li{
        color: $blanco;
    }

    blockquote, a, h1, h2, h3, h4, h5 {
        color: $gris;
    }

    .demo {
        background-color: $grisOscuro;
        border: 1px solid darken($gris, 60);

        .precio {
            color: $verde;
        }

        .icono {
            color: $blanco;
        }

        p {
        color: $gris;
        }
    }

    .paginacion {
        a{
            background-color: $grisOscuro;
        }
        .active {
            background-color: $gris;
        }
    }
    

    .formulario {
        p, label, legend {
            color: $gris;
        }

        input:not([type="submit"]),
        textarea,
        select {
            background-color: $grisOscuro;
            border: 1px solid $grisOscuro;
            color: $blanco;
        }
    }

    .imgPass, .boton-gris-block{
        filter: invert(100%);
    }

    tr{
        color: $blanco;
    }
    thead {
        filter: invert(100%);
    }


    .busqueda{
    
        input {
            background-color: $grisOscuro;
            border: 0.3rem solid lighten($grisOscuro, 8);
            color: $blanco;

        }
        input::placeholder {
            text-decoration-color: none;
            color: lighten($grisOscuro, 35);
        }

        input:focus {
            outline:unset;
            color: $blanco;
        }
    
        button{
            background-color: $gris;
            color: $grisOscuro;
        }
    
    }

    .tarifas{
        .card {
            .titulo, .costos{
                background-color: darken($turquesa, 25);
            }

            .centro{
                background-color: darken($turquesa, 5);
                p, li, h4{
                    color: $grisOscuro;
                }
            }

        }
    }
}