.busqueda{
    margin-top: 4em;
    margin-bottom: 8rem;
    display: flex;
    justify-content: flex-end;
    

    input {
        width: 80%;
        padding: 1rem;
        background-color: lighten($gris, 8);
        border: 0.3rem solid $gris;
        color: $grisOscuro;
        font-weight: bold;
    }
    input::placeholder {
        color: lighten($grisOscuro, 35);
        text-align: center;
        font-size: 1.6rem;
    }

    input:focus {
        outline:unset;
        color: $grisOscuro;
        font-weight: bold;
    }

    button{
        background-color: $grisOscuro;
        width: 30rem;
        color: $blanco;
        font-weight: bold;
        border: none;
    }

}