}
.dark-Mode p, .dark-Mode li {
  color: #FFFFFF;
}
.dark-Mode blockquote, .dark-Mode a, .dark-Mode h1, .dark-Mode h2, .dark-Mode h3, .dark-Mode h4, .dark-Mode h5 {
  color: #e1e1e1;
}
.dark-Mode .demo {
  background-color: #333333;
  border: 1px solid #484848;
}
.dark-Mode .demo .precio {
  color: #71B100;
}
.dark-Mode .demo .icono {
  color: #FFFFFF;
}
.dark-Mode .demo p {
  color: #e1e1e1;
}
