.testimoniales{
    margin: 1rem 0;
}

.testimonial {
        background-color: $turquesa;
        font-size: 2rem;
        padding: 2rem;
        color: $blanco;
        border-radius: 2rem;


        blockquote{
            padding-left: 5rem;
            position: relative;

            &::before{
                content: "";
                   background-image: url(../img/comilla.svg);
                   background-repeat: no-repeat;
                   height: 6rem;
                   width: 8rem;
                   display: block;
                   position: absolute;
                   left: -2rem;
                   top: -1rem;
                }
            }
    p{
        text-align: right;
        color: $blanco;
        font-size: 1.6rem;
    }
}
