.paginacion {
    display: flex;
    justify-content: center;


    ul {
        list-style: none;
        display: flex;
        font-size: 2rem;
        gap: 1rem;
        align-items: center;

        
        a {
            background-color: $gris;
            padding: 1.5rem;
            color: lighten($grisOscuro, 40);
            border-radius: 10%;
            font-weight: bold;

        }
        .disabled {
            display: none;
        }
        .active {
            background-color: darken($gris, 5);
            padding: 1.5rem;
            color: lighten($grisOscuro, 5);
            border-radius: 10%;
            font-weight: bold;
        }
    }

}

